.spinner-container {
	margin-top: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.loading-spinner {
	width: 120px;
	height: 120px;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 16px solid #f3f3f3;
	border-top: 16px solid #333;
	border-radius: 50%;
	animation: spin 1.3s linear infinite; /* Spinning animation */
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
