.quote-button {
	cursor: pointer;
	font-size: 1.2rem;
	height: 2.5rem;
	bottom: none;
	border-radius: 10px;
	color: #fff;
	background: #333;
	outline: none;
	padding: 0.5rem 1.8rem;
	box-shadow: 0 0.3rem rgba(121, 121, 121, 0.65);
	&:hover {
		filter: brightness(110%);
	}
	&:active {
		transform: translate(0, 0.3rem);
		box-shadow: 0 0.1rem rgba(255, 255, 255, 0.3);
	}
}
.twitter-button:hover {
	color: #38a1f3;
}
.fa-twitter {
	font-size: 1.5rem;
}
